import "./App.css";
import { db } from "./firebase";
import { ref, onValue } from "firebase/database";
import { useState, useEffect } from "react";
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Header from './Header';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const theme = createTheme();


function App() {
  const [timesS1, setTimesS1] = useState([]);
  const [timesS2, setTimesS2] = useState([]);
  const [timesS3, setTimesS3] = useState([]);
  const [timesS4, setTimesS4] = useState([]);

  const [isShown, setIsShown] = useState(false);
  const [isShown2, setIsShown2] = useState(false);
  const [isShown3, setIsShown3] = useState(false);
  const [isShown4, setIsShown4] = useState(false);

  const [startDate, setStartDate] = useState(new Date());
  

  const handleClick1 = event => {
    //  toggle shown state
    setIsShown(current => !current);
    setIsShown2(false);
    setIsShown3(false);
    setIsShown4(false);
  };

  const handleClick2 = event => {
    //  toggle shown state
    setIsShown2(current => !current);
    setIsShown(false);
    setIsShown3(false);
    setIsShown4(false);
  };

  const handleClick3 = event => {
    //  toggle shown state
    setIsShown3(current => !current);
    setIsShown2(false);
    setIsShown(false);
    setIsShown4(false);
  };

  const handleClick4 = event => {
    //  toggle shown state
    setIsShown4(current => !current);
    setIsShown2(false);
    setIsShown3(false);
    setIsShown(false);
  };

  const fadeTime = 50;

  //read
  useEffect(() => {
    onValue(ref(db), (snapshot) => {
      setTimesS1([]);
      const data = snapshot.val();
      if (data !== null) {
        var dictionary = Object.values(data).at(0).SensorData1; 
        var keys = Object.keys(dictionary);
        var delta = 2;
        var slice = 10;
        
        console.log(startDate);

        keys.map((time) => (
          setTimesS1((oldArray) => 
          [...oldArray, parseFloat(dictionary[time].substring(dictionary[time].indexOf("Temp:")+5)) + "\xB0F at " + dictionary[time].substring(0,dictionary[time].indexOf("Temp:"))])
        ));
        
        
        setTimesS1(timesS1 => timesS1.reverse());
        setTimesS1(timesS1 => timesS1.filter(function(value, index, Arr) {
          return index % delta === 0;
      }));
        setTimesS1(timesS1 => timesS1.slice(0,slice));

      //   var dictionary2 = Object.values(data).at(0).SensorData2; 
      //   var keys2 = Object.keys(dictionary2);
        
      //   keys2.map((time) => (
      //     setTimesS2((oldArray) => 
      //     [...oldArray, parseFloat(dictionary2[time].substring(dictionary2[time].indexOf("Temp:")+5)) + "\xB0F at " + dictionary2[time].substring(0,dictionary2[time].indexOf("Temp:"))])
      //   ));
        
        
      //   setTimesS2(timesS2 => timesS2.reverse());
      //   setTimesS2(timesS2 => timesS2.filter(function(value, index, Arr) {
      //     return index % delta === 0;
      // }));
      //   setTimesS2(timesS2 => timesS2.slice(0,slice));

      //   var dictionary3 = Object.values(data).at(0).SensorData3; 
      //   var keys3 = Object.keys(dictionary3);
        
      //   keys3.map((time) => (
      //     setTimesS3((oldArray) => 
      //     [...oldArray, parseFloat(dictionary3[time].substring(dictionary3[time].indexOf("Temp:")+5)) + "\xB0F at " + dictionary3[time].substring(0,dictionary3[time].indexOf("Temp:"))])
      //   ));
        
        
      //   setTimesS3(timesS3 => timesS3.reverse());
      //   setTimesS3(timesS3 => timesS3.filter(function(value, index, Arr) {
      //     return index % delta === 0;
      // }));
      //   setTimesS3(timesS3 => timesS3.slice(0,slice));

      //   var dictionary4 = Object.values(data).at(0).SensorData4; 
      //   var keys4 = Object.keys(dictionary4);
        
      //   keys4.map((time) => (
      //     setTimesS4((oldArray) => 
      //     [...oldArray, parseFloat(dictionary4[time].substring(dictionary4[time].indexOf("Temp:")+5)) + "\xB0F at " + dictionary4[time].substring(0,dictionary4[time].indexOf("Temp:"))])
      //   ));
        
        
      //   setTimesS4(timesS4 => timesS4.reverse());
      //   setTimesS4(timesS4 => timesS4.filter(function(value, index, Arr) {
      //     return index % delta === 0;
      // }));
      //   setTimesS4(timesS4 => timesS4.slice(0,slice));
      }
    });
  }, []);


  return (
    
    <ThemeProvider theme={theme}>
      <CssBaseline />


      <div className="App">
      
        <Header/>
        
        <Container
        alignItems='center'
        align='center'
        sx={{
          mt: 5

        }}
        >

        
    
        
          
        <Typography
            component="h2"
            className="fade-list-item"
            style={{ animationDelay: `${0}ms`}}
            variant="h3"
            align="center"
            gutterBottom>
            History
          </Typography>

          <DatePicker 
            
            selected={startDate} 
            onChange={(date:Date) => setStartDate(date)}
            popperPlacement="auto" />

          <Stack
              sx={{ pt: 0,
              mt: 2 }}
              direction="row"
              spacing={2}
              justifyContent="center"
            >
              {/* <Button variant="contained">Contact</Button> */}
              
              <Button variant="contained" onClick={handleClick1}>Sensor 1</Button>
              <Button variant="contained" onClick={handleClick2}>Sensor 2</Button>
              <Button variant="contained" onClick={handleClick3}>Sensor 3</Button>
              <Button variant="contained" onClick={handleClick4}>Sensor 4</Button>
          </Stack>

          {isShown && timesS1.map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${fadeTime * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}
                </Typography>

              </Container>
            </>
          ))}

          {isShown2 && timesS2.map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${fadeTime * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}
                </Typography>

              </Container>
            </>
          ))}

          {isShown3 && timesS3.map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${fadeTime * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}
                </Typography>

              </Container>
            </>
          ))}

          {isShown4 && timesS4.map((temp, index) => (
            
            <>
              <Container
                className="fade-list-item"
                alignItems='center'
                sx={{
                  pt: 2,
                  pb: 2,
                  mt: 5

                }}
                justifyContent='center'
                style={{ animationDelay: `${fadeTime * index}ms`, borderRadius: 20, color: 'white', backgroundColor: '#4287f5' }}
                component="main"
                maxWidth="xs">
                <Typography
                  component="h1"
                  variant="h6"
                  align="center"
                  gutterBottom>
                  {temp}
                </Typography>

              </Container>
            </>
          ))}
          

        </Container>
      </div>
    </ThemeProvider>
  );
}

export default App;
